.App {
  text-align: center;
}

#character-player {
  width: 48px;
  height: 36px;
  color: blue;
  background-image: url(./assets/tonk.png);
}

#character-weapon {
  width: 12px;
  height: 24px;
  transform: rotate(85deg);
  background-image: url(./assets/barrel.png);
  margin-left: 35px;
  margin-top: -30px;
}

div.a {
  position: relative;
  width: relative;
  height: 600px;
  border: 3px solid red;
}
div.b {
  position: absolute;
  left: auto;
  width: 100px;
  height: 500px;
  border: 3px solid blue;
  background-color: black;
} 
div #score {
  position: absolute;
}
a {
  position: relative;
  z-index: 1;
}